import React from 'react';
import { AiFillProject, AiOutlineTeam } from 'react-icons/ai';
import { Link } from 'react-router-dom';
import {
  FaDownload,
  FaFacebook,
  FaGithub,
  FaInstagram,
  FaTwitter,
  FaLinkedin,
  FaHtml5,
  FaCss3,
  FaJs,
  FaWordpress,
  FaPalette,
} from 'react-icons/fa';
import { motion } from 'framer-motion';
import { SiRedux } from 'react-icons/si';
import { HiChatAlt2 } from 'react-icons/hi';
import { kings5, kingsley } from '../../../assets';
import Card from '../../elements/card';
import './about.css';

const About = () => {
  return (
    <motion.section
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      transition={{ delay: 0.5 }}
      className="container-lg pt-5"
    >
      <div className="about-header mb-sm-5 d-flex align-items-center justify-content-center flex-column">
        <div>
          <p className="text-center">
            Get to <span className="text-primary">know</span> me
          </p>
        </div>
        <div>
          <h1 className="text-center">About Me</h1>
        </div>
        <div className="contact-divider bg-primary"></div>
      </div>
      <section className="row mt-5">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="col-md-4"
        >
          <img
            src={kingsley}
            alt="Ajaezo Kingsley putting hands in my pocket"
            className="img-fluid"
          />
        </motion.div>
        <motion.div
          className="col-md-8"
          initial={{ x: 900 }}
          animate={{ x: 0 }}
          exit={{ x: 900 }}
          transition={{ delay: 1, duration: 1 }}
        >
          <h2 className="text-primary content-subtitle d-sm-none mt-4">
            Who am I?
          </h2>
          <h6 className="content-title">
            I am Kingsley Ajaezo a Software Engineer
          </h6>
          <p className="content-description">
            I aspire to be a part of a team that will change the world through
            technology by providing business solutions to industries and
            start-ups through the development of custom software solutions and
            web applications  with a team of like-minded individuals and a
            shared passion I'm also hoping to enter the blockchain niche, being
            the future of the web
          </p>
          <div className="about-info row mt-4">
            <div className="col-md-6">
              <h6 className="font-weight-bold">Name:</h6>
              <span>Ajaezo Kingsley E</span>
            </div>
            <div className="col-md-6">
              <h6 className="font-weight-bold">Email:</h6>
              <span>ajaezokingsley@gmail.com</span>
            </div>
            <div className="col-md-6 mt-4">
              <h6 className="font-weight-bold">Location:</h6>
              <span>Nigeria</span>
            </div>
            <div className="col-md-6 mt-4">
              <h6 className="font-weight-bold">Experience</h6>
              <span>
                3<sup>+</sup>
              </span>
            </div>
          </div>
          <div className="about-info-cta row align-items-center mt-4">
            <div className="col-12 col-md-4 cta-btn">
              <Link
                target="_blank"
                download
                to="/Ajaezo Kingsley Ezenwa.pdf"
                treatas="link"
                className="rounded-pill btn btn-primary 
                text-white px-5 py-3 mt-2 d-flex align-items-center 
                justify-content-between"
              >
                <span>Download CV</span> &nbsp;&nbsp;
                <FaDownload />
              </Link>
            </div>
            <div className="about-spacer bg-dark col-md-4 mt-3"></div>

            <div className="about-icons col-md-4 mt-3">
              <a href="https://twitter.com/ajaezokingsley/" target="_blank">
                <span className="social">
                  <FaTwitter size="25" />
                </span>
              </a>
              &nbsp; &nbsp;
              <a href="https://www.instagram.com/ajaezo/" target="_blank">
                <span className="social">
                  <FaInstagram size="25" />
                </span>
              </a>
              &nbsp; &nbsp;
              <a
                href=" https://www.linkedin.com/in/ajaezokingsley/"
                target="_blank"
              >
                <span className="social">
                  <FaLinkedin size="25" />
                </span>
              </a>
              &nbsp; &nbsp;
              <a href=" https://github.com/Kingscliq" target="_blank">
                <span className="social">
                  <FaGithub size="25" />
                </span>
              </a>
              &nbsp; &nbsp;
              <a
                href="https://web.facebook.com/kingsley.ajaezo/"
                target="_blank"
              >
                <span className="social">
                  <FaFacebook size="25" />
                </span>
              </a>
            </div>
          </div>
        </motion.div>
      </section>
      <section>
        <h1 className="about-title mt-5">Technical Skills</h1>
        <div className="row">
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="HTML"
              icon={<FaHtml5 size="60" color="#009e66" />}
              description="Indept knowledge of modern HTML 5 for building web application frameworks"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="CSS"
              icon={<FaCss3 size="60" color="#009e66" />}
              description="Ability to translate UIs designs into Responsive and cross platform stylesheets"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Javascript"
              icon={<FaJs size="60" color="#009e66" />}
              description="Es6 modern javascript indept knowledge for building scalable and amazing user experience"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Wordpress"
              icon={<FaWordpress size="60" color="#009e66" />}
              description="Build Solutions for business using the power of Php powered Wordpress Framework"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Redux"
              icon={<SiRedux size="60" color="#009e66" />}
              description="I use it for advanced state management for my React Applications"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="UX Design"
              icon={<FaPalette size="60" color="#009e66" />}
              description="I also use graphic design tools for creating amazing designs that converts"
            />
          </div>
        </div>
      </section>
      <section>
        <h1 className="about-title mt-5">Soft Skills</h1>
        <div className="row">
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Project Management"
              icon={<AiFillProject size="60" color="#009e66" />}
              description="Ability to manage Software projects both at medium and Enterprise level"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Amazing Team Player"
              icon={<AiOutlineTeam size="60" color="#009e66" />}
              description="I love working in a team of like minds especially experienced team members I can learn from"
            />
          </div>
          <div
            className="col-12 col-md-4"
            data-aos="fade-up"
            data-aos-duration="2000"
          >
            <Card
              heading="Excellent Communication"
              icon={<HiChatAlt2 size="60" color="#009e66" />}
              description="Ability to translate tech jargons and effectively communicate it to the business team"
            />
          </div>
        </div>
      </section>
      <section>
        <h1 className="h1 mt-5">Resume</h1>
        <section className="row">
          <div className="education col-12 col-md-6">
            <h4 className="mt-5 mb-3">Education</h4>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Computer Science</h4>
              <p className="resume-card-desc">
                Federal College of Agriculuture Ishiagu - 2015-2017
              </p>
              <p className="resume-card-desc">
                The most important aspect of computer science is problem
                solving, which is essential skill for life. Throughout my school
                year, I was exposed to the design, development, and analysis of
                software and hardware used to solve problems in a variety of
                business, scientific, and social contexts.
              </p>
            </div>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Cloud Computing</h4>
              <p className="resume-card-desc">
                Google Africa Developer Scholarship(Cloud Computing Track)/
              </p>
              <p className="resume-card-desc">
                I decided to investigate the world of cloud computing because it
                is the future of information storage and provides a modern,
                efficient method of access to computing resources to
                organizations of all sizes.s
              </p>
            </div>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Diploma in Web Design</h4>
              <p className="resume-card-desc">
                Scholar Int. Institution of Technology, Oregon, USA(Online) /
              </p>
              <p className="resume-card-desc">
                Studying at SIIT helped me through the essential tools for web
                page development, with HTML, CSS, javascript and the amazing IDE
                Adobe Dreamweaver
              </p>
            </div>
          </div>
          <div className="education col-12 col-md-6">
            <h4 className="mt-5 mb-3">Experience</h4>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Software Engineeer</h4>
              <p className="resume-card-desc">
                CrowdForce - April 2021 - present - Remote
              </p>
              <p className="resume-card-desc">
                Currently working as a Software Engineer with Crowdforce, one of
                Nigeria's leading fintech companies, executing and launching new
                projects as well as translating business requirements into
                technical solutions.
              </p>
            </div>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Software Engineeer(Intern)</h4>
              <p className="resume-card-desc">
                Decagon/ CrowdForce/ - February 2021 - April 2021 - Remote
              </p>
              <p className="resume-card-desc">
                During a three-month internship supported by UK-Ng techhub and
                Decagon, I was sent to Crowdforce as a frontEnd Software
                Engineer, where I worked on the frontEnd of one of their major
                applications. Working in a fintech startup, I found myself
                surrounded by a network of diligent and likeminded individuals
                who sincerely believe in the company's product or service and
                are devoted to challenging the status quo of the traditional
                finance industry.
              </p>
            </div>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">Web Developer(Intern)</h4>
              <p className="resume-card-desc">
                Genesys Tech Hub Enugu - June 2020 - February 2020 Enugu, Enugu
                State, Nigeria
              </p>
              <p className="resume-card-desc">
                Learnable is a product development and leadership training
                program for young individuals who want to pursue a career in the
                software development industry. The curriculum is intended to
                provide entry-level technologists with the skills needed for a
                successful career in technology. The program includes classes on
                software development and product design. It also focuses on
                assisting participants in developing soft skills such as
                effective communication, research, marketing, and so on. Being
                at Genesys pushed me past my limits, and when I look back on
                everything I've accomplished, I smile with zero regrets.
              </p>
            </div>
            <div
              className="resume-card"
              data-aos="fade-up"
              data-aos-duration="2000"
            >
              <span className="item-arrow"></span>
              <h4 className="resume-card-title">
                Web Developer and IT Personnel
              </h4>
              <p className="resume-card-desc">
                Century Gateways Technologies- May 2019 - April 2020, Ado Ekiti,
                Ekiti State, Nigeria
              </p>
              <p className="resume-card-desc">
                In the course of my stay at Century Gateways, I taught students
                how to build and develop websites using various web
                technologies. That is, for front-end and back-end development,
                HTML, CSS, Javascript, and PHP, as well as database management
                and Java programming. I instructed and directed students on how
                to host and maintain websites.
              </p>
            </div>
          </div>
        </section>
        <section className="experience"></section>
      </section>
    </motion.section>
  );
};

export default About;
