import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import './navbar.css';
import { AiOutlineBars, AiOutlineCloseCircle } from 'react-icons/ai';
import { FaGithub, FaLinkedin, FaTimes, FaTwitter } from 'react-icons/fa';
import { kingLogo } from '../../../assets';
import { motion } from 'framer-motion';

export const MainNav = ({ mobile, setMobile }) => {
  const logoVariants = {
    init: { rotate: -180 },
    end: {
      rotate: 0,
      transition: { duration: 1 },
    },
  };
  const svgPathVariants = {
    init: {
      opacity: 0,
      pathLength: 0,
    },
    end: {
      opacity: 1,
      pathLength: 1,
      transition: {
        duration: 2,
        ease: 'easeInOut',
      },
    },
  };

  const navLinkWebVariants = {
    init: {},
    end: {},
  };

  return (
    <div className="nav-container pt-2">
      <nav className="container-lg d-flex justify-content-between align-items-center">
        <div className="brand">
          <Link to="/">
            <svg
              id="BACKGROUND"
              style={{
                height: '50px',
                width: '50px',
                // backgroundColor: "#009e66",
              }}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 142.8 166"
              variants={logoVariants}
              initial={'init'}
              animate={'end'}
            >
              <defs></defs>
              <motion.path
                style={{ fill: '#1d1d1d ' }}
                d="M319.61,589l-40,51.51,40,65.91v34.22H294.33l-42.69-75.29-15,18.37L237,708.64H198l-0.37-134h39v68.72h0.77c3.88-6.73,8-12.94,11.9-19.15l33.86-49.57h36.44V589Z"
                transform="translate(-197.59 -574.64)"
                variants={svgPathVariants}
                initial={'init'}
                animate={'end'}
              />
              <motion.path
                style={{ fill: '#009e66' }}
                d="M319.61,574.64H283.17l-33.86,49.57c-3.88,6.21-8,12.42-11.9,19.15h-0.77V574.64h-39l0.37,134H237l-0.39-24.92,15-18.37,42.69,75.29h46.06l-60.8-100.13,51.18-65.87H319.61Z"
                transform="translate(-197.59 -574.64)"
                variants={svgPathVariants}
                initial={'init'}
                animate={'end'}
              />
            </svg>
          </Link>
        </div>
        <div>
          <ul className="active">
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink to="/">Home</NavLink>
            </motion.li>
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink to="/about">About</NavLink>
            </motion.li>

            <motion.li
              transition={{ duration: 0.5 }}
              whileTap={{ scale: 0.8 }}
              whileHover={{ scale: 1.2 }}
            >
              <NavLink to="/portfolio">Portfolio</NavLink>
            </motion.li>
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2 }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink to="/contact">Contact</NavLink>
            </motion.li>
          </ul>
          <div className="app-bar" onClick={() => setMobile(true)}>
            <AiOutlineBars color={'#fff'} size="20" />
          </div>
        </div>
      </nav>
    </div>
  );
};

export const MobileNav = ({ mobile, setMobile }) => {
  const handleMobileNavDisable = () => {
    setMobile(false);
  };
  return (
    <motion.section
      initial={{ scaleY: 0 }}
      animate={{ scaleY: 1 }}
      transition={{ duration: '.5', ease: 'easeOut' }}
      exit={{ scaleY: 0 }}
      className="mobile-nav d-fixed"
    >
      <div className="mobile-nav-container">
        <div className="mobile-nav-close" onClick={() => setMobile(false)}>
          <AiOutlineCloseCircle size={30} />
        </div>
        <div className="mobile-brand mb-2">
          <div>
            <img src={kingLogo} alt="kingsley Ajaezo" height="35" width="35" />
          </div>
        </div>
        <div className="mobile-nav-navlinks">
          <ul>
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2, x: '50px' }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink
                to="/"
                onClick={handleMobileNavDisable}
                className="mobileNavLink"
              >
                Home
              </NavLink>
            </motion.li>
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2, x: '50px' }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink
                to="/about"
                onClick={handleMobileNavDisable}
                className="mobileNavLink"
              >
                About
              </NavLink>
            </motion.li>

            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2, x: '50px' }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink
                to="/portfolio"
                onClick={handleMobileNavDisable}
                className="mobileNavLink"
              >
                Portfolio
              </NavLink>
            </motion.li>
            <motion.li
              transition={{ duration: 0.5 }}
              whileHover={{ scale: 1.2, x: '50px' }}
              whileTap={{ scale: 0.8 }}
            >
              <NavLink
                to="/contact"
                onClick={handleMobileNavDisable}
                className="mobileNavLink"
              >
                Contact
              </NavLink>
            </motion.li>
          </ul>
        </div>
        <div className="mobile-social mt-3">
          <a href=" https://github.com/Kingscliq" target="_blank">
            <div className="mr-1">
              <FaGithub size={20} />
            </div>
          </a>
          <a href="https://twitter.com/ajaezokingsley/" target="_blank">
            <div className="mr-1">
              <FaTwitter size={20} />
            </div>
          </a>
          <a href="https://www.linkedin.com/in/ajaezokingsley/" target="_blank">
            <div className="mr-1">
              <FaLinkedin size={20} />
            </div>
          </a>
        </div>
      </div>
    </motion.section>
  );
};

const NavBar = () => {
  const [mobile, setMobile] = React.useState(false);

  return mobile ? (
    <MobileNav mobile={mobile} setMobile={setMobile} />
  ) : (
    <MainNav mobile={mobile} setMobile={setMobile} />
  );
};

export default NavBar;
