import React from "react";
import "./card.css";

const Card = ({ icon, heading, description }) => {
  return (
    <section className="card services-card">
      <div className="card-icon">{icon}</div>
      <header>
        <h6 className="card-heading">{heading}</h6>
      </header>
      <article className="card-description">
        <p>{description}</p>
      </article>
    </section>
  );
};

export default Card;
